<template>
  <router-view/>
</template>

<script>
export default {
  mounted(){
    this.$store.state.innerheight = window.innerHeight;
    this.$nextTick(() => {//更新后延迟执行后续代码
      window.addEventListener("resize", () => {
        //监听浏览器窗口大小改变
        this.$store.state.innerheight = window.innerHeight;
      });
    });
  },
}
</script>
